<template>
    <div>
        <Header></Header>
        <div class="pad_menu">
            <PadMenu></PadMenu>
        </div>
        <div class="pad_content">
            <el-form ref="nowform" :model="nowform" :rules="rules" label-width="80px" label-position="left"
                     align="left">
                <el-form-item label="" prop="id">
                    <el-input v-model="nowform.id"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="nowform.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="gender">
                    <el-radio-group v-model="nowform.gender">
                        <el-radio label=""></el-radio>
                        <el-radio label=""></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" prop="school">
                    <el-input v-model="nowform.school"></el-input>
                </el-form-item>
                <el-form-item label="" prop="major">
                    <el-input v-model="nowform.major"></el-input>
                </el-form-item>
                <el-form-item label="" prop="qualification">
                    <el-input v-model="nowform.qualification"></el-input>
                </el-form-item>
                <el-form-item label="" align="center" prop="cstGraduation">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="" v-model="nowform.cstGraduation"
                                        style="width: 100%;"></el-date-picker>
                    </el-col>
                </el-form-item>
                <el-form-item label="" align="center" prop="cstEntry">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="" v-model="nowform.cstEntry"
                                        style="width: 100%;"></el-date-picker>
                    </el-col>
                </el-form-item>

                <!--  ##############################################-->
                <el-form-item label="" align="center" prop="nowPersonRanks" v-show="isEdit">
                    <el-timeline>
                        <el-timeline-item style="text-align: left;"
                                          v-for="(nowPersonRank, index) in nowPersonRanks"
                                          :key="index" :color="nowPersonRank.color"
                                          :timestamp="nowPersonRank.cstChange">
                            {{nowPersonRank.rankname}}--{{nowPersonRank.reason}}
                        </el-timeline-item>
                    </el-timeline>
                    <div style="text-align: left;">
                        <el-tooltip effect="light" content="" placement="left">
                            <el-button type="danger" plain @click="deletePersonRank()"></el-button>
                        </el-tooltip>
                        <el-button type="primary" plain @click="addPersonRank()"></el-button>
                        <!--  dialog -->
                        <el-dialog title="/" :visible.sync="addRankDialogVisible">
                            <el-form :model="addRankForm" label-width="100px" label-position="left" align="left"
                                     style="margin-left: 60px" :rules="rules" ref="nowpersonRankForm">
                                <el-form-item label="" style="margin-bottom: 20px">
                                    <el-select v-model="selectRank" placeholder="">
                                        <el-option v-for="rank in ranks" :key="rank.id" :label="rank.name"
                                                   :value="rank.name"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="/" prop="cstChange" style="margin-bottom: 20px">
                                    <el-col :span="11">
                                        <el-date-picker type="date" placeholder=""
                                                        v-model="addRankForm.cstChange"></el-date-picker>
                                    </el-col>
                                </el-form-item>
                                <el-form-item label="/" prop="reason">
                                    <el-input v-model="addRankForm.reason"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="addRankDialogVisible = false"> </el-button>
                                <el-button type="primary" @click="submitAddRank('nowpersonRankForm')"> </el-button>
                            </div>
                        </el-dialog>
                    </div>
                </el-form-item>
                <!--  ##############################################-->

                <el-form-item label="" prop="department">
                    <el-input v-model="nowform.department"></el-input>
                </el-form-item>
                <el-form-item label="" prop="post">
                    <el-input v-model="nowform.post"></el-input>
                </el-form-item>
                <el-form-item label="" align="center" prop="cstResign">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="" v-model="nowform.cstResign"
                                        style="width: 100%;"></el-date-picker>
                    </el-col>
                </el-form-item>
                <el-form-item label="" prop="resignReason">
                    <el-input v-model="nowform.resignReason"></el-input>
                </el-form-item>
                <el-form-item label="" prop="phone">
                    <el-input v-model="nowform.phone"></el-input>
                </el-form-item>

                <!--  -->
                <el-form-item label="" v-show="isEdit">
                    <el-upload ref="upload" :on-preview="handlePreview" :on-remove="handleRemove" :auto-upload="false"
                               :file-list="uploadFileList" :data="nowform"
                               action="http://47.111.235.119:39081/hypad/upload">
                        <!-- action="http://192.168.90.82:39081/hypad/upload">-->
                        <el-button slot="trigger" size="small" type="primary"></el-button>
                        <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"
                                   v-show="isEdit">
                        </el-button>
                        <div slot="tip" class="el-upload__tip"> / 200Mb</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="" v-show="isEdit">
                    <el-table :data="files" stripe style="width: 100%">
                        <el-table-column :label="0" label="" min-width="300" prop="name" show-overflow-tooltip></el-table-column>
                        <el-table-column label="" width="200">
                            <template slot-scope="scope">
                                <el-button type="text" @click="downloadFile(scope.row)" size="small"></el-button>
                                <el-button @click="deletefile(scope.row)" type="text" size="small"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
                <!-- -->

                <el-form-item label="" prop="more">
                    <el-input v-model="nowform.more"></el-input>
                </el-form-item>
                <el-form-item v-show="!isWatch">
                    <el-button type="primary" @click="onSubmit('nowform')"> / </el-button>
                    <el-button @click="resetForm('nowform')"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import PadMenu from "../components/PadMenu";
    import Header from "../components/Header";
    import ElementUI from "element-ui";

    export default {
        name: "NowPersonEdit",
        components: {PadMenu, Header},
        data() {
            return {
                nowform: {
                    id: '',
                    name: '',
                    gender: '',
                    cstEntry: '',
                    ranknum: 0,
                    cstResign: '',
                    resignReason: '',
                    department: '',
                    post: '',
                    school: '',
                    major: '',
                    qualification: '',
                    cstGraduation: '',
                    phone: '',
                    more: ''
                },
                selectRankVisible: false,
                rankname: '',
                selectRank: '',
                addRankForm: {
                    personid: '',
                    rankid: '1',
                    shunxu: 0,
                    reason: '',
                    cstChange: ''
                },
                nowPersonRanks: [],
                isWatch: false,
                isEdit: false,
                ranks: [],
                addRankDialogVisible: false,
                uploadFileList: [],
                files: [],
                rules: {
                    id: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    cstEntry: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    department: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    post: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    cstGraduation: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    cstChange: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            onSubmit(formName) {
                /*  :rule  validate */
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        const _this = this
                        this.nowform.gender = this.nowform.gender == '' ? '1' : '0'
                        console.log("success submit!!!")
                        /*  Jackson  cstCreate  */
                        this.$axios.post('/nowperson/edit', this.nowform, {}).then(result => {
                            // 
                            this.$router.push('/nowperson')
                        })
                    } else {
                        console.log('error submit!!!')
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            /*  */
            deletePersonRank() {
                if (this.nowPersonRanks.length == 0) {
                    return
                }
                this.$axios.get('/nowpersonrank/delete/' +
                    this.nowPersonRanks[this.nowPersonRanks.length - 1].id).then(result => {
                    location.reload()
                })
            },
            /*  dialog */
            addPersonRank() {
                // 
                var date = new Date();
                this.addRankForm.cstChange = date.toISOString()

                const _this = this
                this.$axios.get('/ranks').then(result => {
                    _this.addRankDialogVisible = true
                    _this.ranks = result.data.data
                })
            },
            // 
            submitAddRank(formName) {
                if (this.selectRank == '') {
                    ElementUI.Message.error("")
                    return
                }
                // 
                this.addRankForm.personid = this.nowform.id
                this.addRankForm.shunxu = this.nowPersonRanks.length + 1
                //  rankname  rankid
                for (let rank of this.ranks) {
                    if (this.selectRank == rank.name) {
                        this.addRankForm.rankid = rank.id
                        break
                    }
                }
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$axios.post('/nowpersonrank/add', this.addRankForm, {}).then(result => {
                            location.reload()
                            this.addRankDialogVisible = false
                        })
                    } else {
                        console.log('error submit!!!')
                        return false;
                    }
                });
            },
            /*  */
            submitUpload() {
                this.$refs.upload.submit();
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                });
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            downloadFile(row) {
                console.log(this.nowform.id+'###########')
                window.location.href = 'http://47.111.235.119:39081/hypad/downloadFile/' + this.form.id + '-' + row.name
                /*window.location.href = 'http://192.168.90.82:39081/hypad/downloadFile/' + this.nowform.id + '-' + row.name*/
            },
            deletefile(row) {
                this.$axios.get('/deleteFile/' + this.nowform.id + '-' + row.name).then(res => {

                })
                location.reload()
            }
        },
        created() {
            var nowpersonId = this.$route.params.nowpersonId
            const _this = this

            // 
            if (nowpersonId) {
                // /12
                if (nowpersonId.charAt(0) == '1')
                    this.isWatch = true
                nowpersonId = nowpersonId.substring(1, nowpersonId.length)

                // 
                this.isEdit = true
                this.$axios.get('/nowperson/' + nowpersonId).then(result => {
                    const nowperson = result.data.data
                    _this.nowform = nowperson
                    _this.nowform.gender = nowperson.gender == 1 ? '' : ''
                })

                // 
                this.$axios.get('/nowpersonrank/' + nowpersonId).then(async function (result) {
                    var nowRanks = result.data.data

                    for (let v in nowRanks) {
                        await _this.$axios.get('/rank/' + nowRanks[v].rankid).then(result2 => {
                            nowRanks[v].color = '#0bbd87'
                            nowRanks[v].cstChange = nowRanks[v].cstChange.substr(0, 10)
                            var rankdetail = result2.data.data
                            nowRanks[v].rankname = rankdetail.name
                            nowRanks[v].decription = rankdetail.decription
                        })
                    }
                    _this.nowPersonRanks = nowRanks
                })

                // 
                this.$axios.get("/filename/" + nowpersonId).then(res => {
                    const keysdata = res.data.data
                    for (let v in keysdata) {
                        var tabledata = {name: 'null'};
                        tabledata.name = keysdata[v];
                        _this.files.push(tabledata)
                    }
                })
            }
        }

    }
</script>

<style scoped>

</style>