<template>
    <div>
        <Header></Header>
        <div class="pad_menu">
            <PadMenu></PadMenu>
        </div>

        <div class="pad_content">
            <el-form ref="form" :model="form" :rules="rules" label-width="80px" label-position="left" align="left">
                <el-form-item label="" prop="name">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-checkbox-group v-model="keysname" v-for="key in keysall">
                        <el-checkbox :label="key.keyname" name="type"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item label="" prop="gender">
                    <el-radio-group v-model="form.gender">
                        <el-radio label=""></el-radio>
                        <el-radio label=""></el-radio>
                    </el-radio-group>
                </el-form-item>
                <!--<el-form-item label="" prop="gender">
                    <el-select v-model="form.gender" placeholder="">
                        <el-option label="" value="man"></el-option>
                        <el-option label="" value="women"></el-option>
                    </el-select>
                </el-form-item>-->
                <el-form-item label="" prop="school">
                    <el-input v-model="form.school"></el-input>
                </el-form-item>
                <el-form-item label="" prop="major">
                    <el-input v-model="form.major"></el-input>
                </el-form-item>
                <el-form-item label="" prop="qualification">
                    <el-input v-model="form.qualification"></el-input>
                </el-form-item>
                <el-form-item label="" prop="workexp">
                    <el-input type="age" v-model="form.workexp" placeholder="!"></el-input>
                </el-form-item>
                <el-form-item label="" prop="salarynow">
                    <el-input v-model="form.salarynow"></el-input>
                </el-form-item>
                <el-form-item label="" prop="salaryexpect">
                    <el-input v-model="form.salaryexpect"></el-input>
                </el-form-item>
                <el-form-item label="" align="center" prop="induction">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="" v-model="form.induction"
                                        style="width: 100%;"></el-date-picker>
                    </el-col>
                    <!--<el-col class="line" :span="2">-</el-col>
                    <el-col :span="11">
                        <el-time-picker placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
                    </el-col>-->
                </el-form-item>
                <el-form-item label="" prop="employfrom">
                    <el-input v-model="form.employfrom"></el-input>
                </el-form-item>
                <el-form-item label="" align="center" prop="gmt_report">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="" v-model="form.gmt_report"
                                        style="width: 100%;"></el-date-picker>
                    </el-col>
                </el-form-item>
                <el-form-item label="/" prop="lightcomp">
                    <el-input v-model="form.lightcomp"></el-input>
                </el-form-item>
                <el-form-item label="" prop="isinterview">
                    <el-radio-group v-model="form.isinterview">
                        <el-radio label=""></el-radio>
                        <el-radio label=""></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" prop="interviewres">
                    <el-radio-group v-model="form.interviewres">
                        <el-radio label=""></el-radio>
                        <el-radio label=""></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" v-show="isEdit">
                    <el-upload ref="upload" :on-preview="handlePreview" :on-remove="handleRemove" :auto-upload="false"
                               :file-list="fileList" :data="form"
                               action="http://47.111.235.119:39081/hypad/upload">
                        <!-- action="http://192.168.90.82:39081/hypad/upload">-->
                        <el-button slot="trigger" size="small" type="primary"></el-button>
                        <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"
                                   v-show="isEdit">
                        </el-button>
                        <div slot="tip" class="el-upload__tip"> / 200Mb</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="" v-show="isEdit">
                    <el-table :data="files" stripe style="width: 100%">
                        <el-table-column :label="0" label="" min-width="300" prop="name" show-overflow-tooltip></el-table-column>
                        <el-table-column label="" width="200">
                            <template slot-scope="scope">
                                <el-button type="text" @click="downloadFile(scope.row)" size="small"></el-button>
                                <el-button @click="deletefile(scope.row)" type="text" size="small"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
                <el-form-item label="/">
                    <el-input type="textarea" v-model="form.workset"></el-input>
                </el-form-item>
                <el-form-item label="" prop="phone">
                    <el-input v-model="form.phone"></el-input>
                </el-form-item>
                <el-form-item label="" prop="email">
                    <el-input v-model="form.email"></el-input>
                </el-form-item>
                <el-form-item v-show="!isWatch">
                    <el-button type="primary" @click="onSubmit('form')"> / </el-button>
                    <el-button @click="resetForm('form')"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import PadMenu from "../components/PadMenu";
    import Header from "../components/Header";

    export default {
        name: "PersonEdit",
        components: {PadMenu, Header},
        data() {
            return {
                form: {
                    id: '0',
                    name: '',
                    gender: '',
                    school: '',
                    major: '',
                    qualification: '',
                    workexp: '',
                    salarynow: '',
                    salaryexpect: '',
                    induction: '',
                    employfrom: '',
                    lightcomp: '',
                    isinterview: '',
                    interviewres: '',
                    workset: '',
                    gmt_report: '',
                    phone: '',
                    email: '',
                    is_delete: '',
                    gmt_create: '',
                    gmt_modified: ''
                },
                index: 0,
                files: [],
                isEdit: false,
                isWatch: false,
                keys: [],
                keysname: [],
                keysall: [], /* key  */
                fileList: [],
                rules: {
                    /* namegender  prop */
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    gender: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    workexp: [
                        {required: true, message: ''}
                        /*{type: 'number', message: ''}*/
                    ],
                    induction: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    isinterview: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                }
            };
        },
        methods: {
            onSubmit(formName) {
                /*  :rule  validate */
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        const _this = this
                        this.form.gender = this.form.gender == '' ? '1' : '0'
                        this.form.isinterview = this.form.isinterview == '' ? '1' : '0'
                        this.form.interviewres = this.form.interviewres == '' ? '1' : '0'
                        console.log("success submit!!!")
                        /*  gmt_create , */
                        this.$axios.post('/person/edit', this.form, {}).then(result => {
                            // ,result  Id
                            var temp = result.data.data + '-' + _this.keysname.join('-')
                            _this.$axios.get('/personkey/edit/' + temp).then(res2 => {
                                _this.$router.push('/')
                            })
                        })
                    } else {
                        console.log('error submit!!!')
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            submitUpload() {
                this.$refs.upload.submit();
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                });
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            downloadFile(row) {
                window.location.href = 'http://47.111.235.119:39081/hypad/downloadFile/' + this.form.id + '-' + row.name
                /*window.location.href = 'http://192.168.90.82:39081/hypad/downloadFile/' + this.form.id + '-' + row.name*/
            },
            deletefile(row) {
                this.$axios.get('/deleteFile/' + this.form.id + '-' + row.name).then(res => {

                })
                location.reload()
            }
        },
        created() {
            var personId = this.$route.params.personId
            const _this = this

            // 
            this.$axios.get("/keys").then(res => {
                const keysdata = res.data.data
                _this.keysall = keysdata
                for (let v in keysdata) {
                    _this.keys.push(keysdata[v].keyname)
                }
            })
            // 
            if (personId) {
                // /12
                if (personId.charAt(0) == '1')
                    this.isWatch = true
                personId = personId.substring(1, personId.length)
                // 
                this.isEdit = true
                this.$axios.get('/person/' + personId).then(result => {
                    const person = result.data.data
                    _this.form = person
                    _this.form.gender = person.gender == 1 ? '' : ''
                    _this.form.isinterview = person.isinterview == 1 ? '' : ''
                    _this.form.interviewres = person.interviewres == 1 ? '' : ''
                })

                // 
                this.$axios.get("/personkey/" + personId).then(res => {
                    const keysdata = res.data.data
                    for (let v in keysdata) {
                        _this.keysname.push(keysdata[v])
                    }
                })
                // 
                this.$axios.get("/filename/" + personId).then(res => {
                    const keysdata = res.data.data
                    for (let v in keysdata) {
                        var tabledata = {name: 'a'};
                        tabledata.name = keysdata[v];
                        _this.files.push(tabledata)
                    }
                })
            }
        }
    }
</script>

