<template>
    <div class="pad_content">
        <div class="div_table">
            <el-table :data="nowpersons" stripe style="width: 100%">
                <el-table-column prop="id" label="" width="100" show-overflow-tooltip></el-table-column>
                <el-table-column prop="name" label="" width="100"></el-table-column>
                <!--<el-table-column prop="keysname" label="" width="100"></el-table-column>-->
                <el-table-column prop="cstEntry" label="" width="100"></el-table-column>
                <el-table-column prop="department" label="" width="100"></el-table-column>
                <el-table-column prop="post" label="" width="100"></el-table-column>
                <!-- /-->
                <el-table-column prop="status" label="" width="60"></el-table-column>
                <el-table-column prop="phone" label="" width="120"></el-table-column>
                <el-table-column prop="more" label="/" show-overflow-tooltip min-width="120px"></el-table-column>

                <el-table-column label="" width="200">
                    <template slot-scope="scope">
                        <el-dialog title="" :visible.sync="dialogVisible" width="500px"
                                   :before-close="handleClose">
                            <el-input type="textarea" :rows="10" placeholder="!!"
                                      v-model="sharedInfo"></el-input>
                            <span slot="footer" class="dialog-footer">
                                    <el-button @click="dialogVisible = false"> </el-button>
                                    <el-button type="primary" v-clipboard:copy="sharedInfo"
                                               @click="onCopy()"> </el-button>
                                </span>
                        </el-dialog>
                        <el-button type="text" @click="onClickShare(scope.row)" size="small"></el-button>
                        <el-button @click="editperson(scope.row,1)" type="text" size="small"></el-button>
                        <el-button @click="editperson(scope.row,2)" type="text" size="small"></el-button>
                        <el-button @click="deleteperson(scope.row)" type="text" size="small"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <!--  -->
        <el-pagination background layout="prev, pager, next" :current-page="currentPage"
                       :page-size="pageSize" @current-change=loadPage :total="total" style="margin-bottom: 40px">
        </el-pagination>
    </div>
</template>

<script>
    import ElementUI from "element-ui";

    export default {
        name: "NowPersonList",
        data() {
            return {
                currentPage: 1, // 
                total: 0,        // 
                pageSize: 10,     // 
                nowpersons: [],      // 
                dialogVisible: false,    // 
                sharedInfo: 'null',      // 
                shareKeysname: [],
                tooltip: true
            }
        },
        methods: {
            loadPage(currentPage) {
                const _this = this
                this.$axios.get("/nowperson?currentPage=" + currentPage).then(res => {
                    var recordss = res.data.data.records
                    for (var v of recordss) {
                        v.cstEntry = v.cstEntry.substr(0, 10)
                        v.gender = v.gender == 1 ? '' : ''
                        v.status = v.cstResign == null ? '' : ''
                    }
                    _this.nowpersons = recordss
                    _this.total = res.data.data.total
                    _this.pageSize = res.data.data.pageSize
                    _this.currentPage = res.data.data.currentPage
                })
            },
            editperson(row, n) {
                // n /12
                this.$router.push('/nowperson/' + n + '' + row.id + '/edit')
            },
            deleteperson(row) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    const _this = this
                    this.$axios.get("/nowperson/delete/" + row.id).then(res => {
                    })
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                    location.reload()
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            handleClose(done) {
                this.$confirm('')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {
                    });
            },
            onClickShare(row) {
                this.dialogVisible = true

                const _this = this

                this.sharedInfo = '' + row.name + '\n'
                    + '' + row.phone + '\n'
                    + '' + row.gender + '\n'
                    + '' + row.cstEntry


                // 
                this.$axios.get("/lastRank/" + row.id).then(async function (result) {
                    var lastRank = result.data.data
                    if (lastRank != null) {
                        await _this.$axios.get("/rank/" + lastRank.rankid).then(res => {
                            _this.sharedInfo += '\n' + '' + res.data.data.name
                        })
                    }
                    _this.sharedInfo += '\n'
                        + '' + row.department + '\n'
                        + '' + row.post + '\n'
                        + '' + (row.cstResign == null ? '' : row.cstResign) + '\n'
                        + '' + (row.cstResign == null ? '' : row.resignReason) + '\n'
                        + '' + (row.school == null ? '' : row.school) + '\n'
                        + '' + (row.major == null ? '' : row.major) + '\n'
                        + '' + (row.qualification == null ? '' : row.qualification) + '\n'
                        + '' + row.cstGraduation.substr(0,10) + '\n'
                        + '/' + (row.workset == null ? '' : row.workset)
                })
            },
            onCopy() {
                this.dialogVisible = false
                ElementUI.Message.success("")
            }
        },
        // 
        created() {
            this.loadPage(1)    // 
        }
    }
</script>

<style scoped>
    .div_table {
        margin-bottom: 20px;
    }
</style>