<template>
    <div>
        <Header></Header>
        <div class="pad_menu">
            <PadMenu></PadMenu>
        </div>

        <div class="pad_content">
            <div class="div_term">
                <!--  -->
                <div class="div_item">
                    <el-checkbox-group v-model="keysname" v-for="key in keysall">
                        <el-checkbox :label="key.keyname" style="margin-right: 4px; float: left"></el-checkbox>
                    </el-checkbox-group>
                </div>
                <br>
                <el-divider class="el_divider" style=""></el-divider>
                <div class="div_item">
                    <el-input placeholder="" prefix-icon="el-icon-search" v-model="nameser"></el-input>
                </div>
                <el-divider class="el_divider" style=""></el-divider>
                <div class="div_item">
                    <el-input placeholder="" prefix-icon="el-icon-search" v-model="lightcompser"></el-input>
                </div>
                <el-divider class="el_divider" style=""></el-divider>
                <el-button type="primary" plain class="div_item" style="margin-top: 12px" @click="search"></el-button>
            </div>
            <div class="div_table">
                <el-table :data="persons" stripe style="width: 100%">
                    <el-table-column prop="name" label="" width="100"></el-table-column>
                    <!--<el-table-column prop="keysname" label="" width="100"></el-table-column>-->
                    <el-table-column prop="lightcomp" label="" width="200"
                                     show-overflow-tooltip></el-table-column>
                    <el-table-column prop="phone" label="" width="120"></el-table-column>
                    <el-table-column prop="gender" label="" width="60"></el-table-column>
                    <el-table-column prop="workexp" label="()" width="100"></el-table-column>
                    <el-table-column prop="induction" label="" width="100"></el-table-column>
                    <el-table-column prop="workset" label="/"></el-table-column>
                    <el-table-column label="" width="200">
                        <template slot-scope="scope">
                            <el-dialog title="" :visible.sync="dialogVisible" width="500px"
                                       :before-close="handleClose">
                                <el-input type="textarea" :rows="10" placeholder="!!"
                                          v-model="sharedInfo"></el-input>
                                <span slot="footer" class="dialog-footer">
                                    <el-button @click="dialogVisible = false"> </el-button>
                                    <el-button type="primary" v-clipboard:copy="sharedInfo"
                                               @click="onCopy()"> </el-button>
                                </span>
                            </el-dialog>
                            <el-button type="text" @click="onClickShare(scope.row)" size="small"></el-button>
                            <el-button @click="editperson(scope.row,1)" type="text" size="small"></el-button>
                            <el-button @click="editperson(scope.row,2)" type="text" size="small"></el-button>
                            <el-button @click="deleteperson(scope.row)" type="text" size="small"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
    import Header from "../components/Header"
    import PadMenu from "../components/PadMenu";
    import ElementUI from "element-ui";

    export default {
        name: "PersonSearch",
        components: {PadMenu, Header},
        data() {
            return {
                currentPage: 1, // 
                total: 0,        // 
                pageSize: 10,     // 
                persons: [],
                keysnamec: '',
                keys: [],
                keysname: [],
                keysall: [],
                nameser: '',
                lightcompser: '',
                workexpser: '',
                dialogVisible: false,    // 
                sharedInfo: 'test',      // 
                shareKeysname: []
            }
        },
        methods: {
            search() {
                const _this = this
                // 
                var temp = this.keysname.join('-')
                temp = temp + "|" + this.nameser + '|' + this.lightcompser
                _this.$axios.get('/searchMult/' + temp).then(res => {
                    var persondata = res.data.data
                    for (let v in persondata) {
                        persondata[v].induction = persondata[v].induction.substr(0, 10)
                        persondata[v].gender = persondata[v].gender == 1 ? '' : ''
                    }
                    _this.persons = persondata
                })
            },
            loadPage(currentPage) {
                const _this = this
                // 
                this.$axios.get("/keys").then(res => {
                    const keysdata = res.data.data
                    _this.keysall = keysdata
                    for (let v in keysdata) {
                        _this.keys.push(keysdata[v].keyname)
                    }
                })

                this.$axios.get("/person?currentPage=" + currentPage).then(res => {
                    var recordss = res.data.data.records
                    for (var v of recordss) {
                        v.induction = v.induction.substr(0, 10)
                        v.gender = v.gender == 1 ? '' : ''
                        _this.$axios.get("/personkey/" + v.id).then(res2 => {
                            _this.keysnamec = res2.data.data.toString()
                        })
                    }
                    _this.persons = recordss
                    _this.currentPage = res.data.data.current
                    _this.total = res.data.data.total
                    _this.pageSize = res.data.data.size
                })
            },
            editperson(row, n) {
                // n /12
                this.$router.push('/person/' + n + '' + row.id + '/edit')
            },
            deleteperson(row) {
                const _this = this
                this.$axios.get("/person/delete/" + row.id).then(res => {

                })
                location.reload()
            },
            handleClose(done) {
                this.$confirm('')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {
                    });
            },
            onClickShare(row) {
                this.dialogVisible = true

                const _this = this
                this.$axios.get("/personkey/" + row.id).then(res => {
                    const keysdata = res.data.data
                    _this.shareKeysname.length = 0
                    for (let v in keysdata) {
                        _this.shareKeysname.push(keysdata[v])
                    }
                    _this.sharedInfo = '' + row.name + '\n' +
                        '' + row.phone + '\n' +
                        '' + row.email + '\n' +
                        '' + row.gender + '\n' +
                        '' + (row.school == null ? '' : row.school) + '\n' +
                        '' + (row.major == null ? '' : row.major) + '\n' +
                        '' + (row.qualification == null ? '' : row.qualification) + '\n' +
                        '' + _this.shareKeysname.toString() + '\n' +
                        '' + row.workexp + '\n' +
                        '' + (row.salarynow == null ? '' : row.salarynow) + '\n' +
                        '' + (row.salaryexpect == null ? '' : row.salaryexpect) + '\n' +
                        '' + row.induction + '\n' +
                        '' + (row.employfrom == null ? '' : row.employfrom) + '\n' +
                        '' + (row.gmt_report == null ? '' : row.gmt_report) + '\n' +
                        '' + (row.isinterview == 1 ? '' : '') + '\n' +
                        '' + (row.interviewres == 1 ? '' : '') + '\n' +
                        '/' + (row.workset == null ? '' : row.workset) + '\n' +
                        '/' + (row.lightcomp == null ? '' : row.lightcomp) + '\n'
                })
            },
            onCopy() {
                this.dialogVisible = false
                ElementUI.Message.success("")
            }

        },
        // 
        created() {
            this.loadPage(1)    // 
        }
    }
</script>

<style scoped>
    .person_menu {
        width: 20%;
        float: left;
    }

    .div_table {
        margin-top: 20px;
        margin-bottom: 40px;
    }

    .div_term {
        border: 2px solid #00c4ff;
        text-align: left;
    }

    .div_item {
        margin-top: 12px;
        margin-left: 15px;
        margin-bottom: 12px;
    }

    .el_divider {
        margin-top: 10px;
        margin-bottom: 0px;
    }
</style>