<template>
    <div class="padmenu">
        <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">
            <el-radio-button :label="false"></el-radio-button>
            <el-radio-button :label="true"></el-radio-button>
        </el-radio-group>
        <el-menu default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
                 :collapse="isCollapse">
            <el-submenu index="1">
                <template slot="title">
                    <i class="el-icon-user-solid"></i>
                    <span slot="title"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="1-1" @click="goPersonOne"></el-menu-item>
                    <el-menu-item index="1-2" @click="goPersonSearchOne"></el-menu-item>
                    <el-menu-item index="1-3" @click="goPersonEditOne"></el-menu-item>
                    <el-menu-item index="1-4" @click="goKeysOne"></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="2">
                <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title"></span>
                </template>
                <el-menu-item-group>
                    <el-menu-item index="2-1" @click="goPersonTwo"></el-menu-item>
                    <el-menu-item index="2-2" @click="goPersonSearchTwo"></el-menu-item>
                    <el-menu-item index="2-3" @click="goPersonEditTwo"></el-menu-item>
                    <el-menu-item index="2-4" @click="goRankTwo"></el-menu-item>
                    <el-menu-item index="2-5" @click="goAccount"></el-menu-item>
                </el-menu-item-group>
            </el-submenu>
            <el-menu-item index="3" disabled>
                <i class="el-icon-document"></i>
                <span slot="title"></span>
            </el-menu-item>
            <el-menu-item index="4" disabled>
                <i class="el-icon-setting"></i>
                <span slot="title"></span>
            </el-menu-item>
        </el-menu>
    </div>
</template>

<script>
    export default {
        name: "padmenu",
        data() {
            return {
                isCollapse: false
            }
        },
        methods: {
            goPersonOne() {
                this.$router.push("/person")
            },
            goPersonSearchOne() {
                this.$router.push("/PersonSearch")
            },
            goPersonEditOne() {
                this.$router.push("/person/add")
            },
            goKeysOne() {
                this.$router.push("/keys")
            },
            goPersonTwo(){
                this.$router.push("/nowperson").catch(()=>{})
            },
            goPersonSearchTwo(){
                this.$router.push("/nowpersonsearch").catch(()=>{})
            },
            goPersonEditTwo(){
                this.$router.push("/nowperson/add").catch(()=>{})
            },
            goRankTwo(){
                this.$router.push("/rank").catch(()=>{})
            },
            goAccount(){
                this.$router.push("/account")
            },
            handleOpen(key, keyPath) {

            }, handleClose(key, keyPath) {

            }
        }
    }
</script>

<style scoped>
    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
        min-height: 400px;
    }

    .padmenu {
        margin-top: 20px;
    }
</style>